import { CubismModel } from '../model/cubismmodel';
import { ACubismMotion } from './acubismmotion';
import { CubismMotionQueueEntry } from './cubismmotionqueueentry';

// exp3.json
const DefaultFadeTime = 1.0;

/**
 * 
 *
 * 
 */
export class CubismExpressionMotion extends ACubismMotion {
  /**
   * 
   * @param json exp
   * @param size 
   * @return 
   */
  public static create(json: CubismSpec.ExpressionJSON): CubismExpressionMotion {
    const expression: CubismExpressionMotion = new CubismExpressionMotion();

    const fadeInTime = json.FadeInTime;
    const fadeOutTime = json.FadeOutTime;

    expression.setFadeInTime(fadeInTime !== undefined ? fadeInTime : DefaultFadeTime); // 
    expression.setFadeOutTime(fadeOutTime !== undefined ? fadeOutTime : DefaultFadeTime); // 

    // 
    const parameters = json.Parameters || [];

    for (let i = 0; i < parameters.length; ++i) {
      const param = parameters[i];
      const parameterId: string = param.Id; // ID

      const value: number = param.Value; // 

      // 
      let blendType: ExpressionBlendType;

      switch (param.Blend) {
        case 'Multiply':
          blendType = ExpressionBlendType.ExpressionBlendType_Multiply;
          break;

        case 'Overwrite':
          blendType = ExpressionBlendType.ExpressionBlendType_Overwrite;
          break;

        case 'Add':
        //  
        default:
          blendType = ExpressionBlendType.ExpressionBlendType_Add;
          break;
      }

      // 
      const item: ExpressionParameter = {
        parameterId: parameterId,
        blendType: blendType,
        value: value,
      };

      expression._parameters.push(item);
    }

    return expression;
  }

  /**
   * 
   * @param model 
   * @param userTimeSeconds []
   * @param weight 
   * @param motionQueueEntry CubismMotionQueueManager
   */
  public doUpdateParameters(
    model: CubismModel,
    userTimeSeconds: number,
    weight: number,
    motionQueueEntry: CubismMotionQueueEntry,
  ): void {
    for (let i = 0; i < this._parameters.length; ++i) {
      const parameter: ExpressionParameter = this._parameters[i];

      switch (parameter.blendType) {
        case ExpressionBlendType.ExpressionBlendType_Add: {
          model.addParameterValueById(
            parameter.parameterId,
            parameter.value,
            weight,
          );
          break;
        }
        case ExpressionBlendType.ExpressionBlendType_Multiply: {
          model.multiplyParameterValueById(
            parameter.parameterId,
            parameter.value,
            weight,
          );
          break;
        }
        case ExpressionBlendType.ExpressionBlendType_Overwrite: {
          model.setParameterValueById(
            parameter.parameterId,
            parameter.value,
            weight,
          );
          break;
        }
        default:
          // 
          break;
      }
    }
  }

  /**
   * 
   */
  constructor() {
    super();

    this._parameters = [];
  }

  _parameters: ExpressionParameter[]; // 
}

/**
 * 
 */
export enum ExpressionBlendType {
  ExpressionBlendType_Add = 0, // 
  ExpressionBlendType_Multiply = 1, // 
  ExpressionBlendType_Overwrite = 2 // 
}

/**
 * 
 */
export interface ExpressionParameter {
  parameterId: string; // ID
  blendType: ExpressionBlendType; // 
  value: number; // 
}
