import { CubismRenderer } from './rendering/cubismrenderer';
import { CubismLogInfo, CubismLogWarning } from './utils/cubismdebug';

// 

let s_isStarted = false;
let s_isInitialized = false;
let s_option: CubismStartupOption | undefined = undefined;

/**
 * Framework
 */
export namespace Constant {
  export const vertexOffset = 0; // 
  export const vertexStep = 2; // 
}

/**
 * Live2D Cubism SDK Original Workflow SDK
 * CubismFramework.initialize()CubismFramework.dispose()
 */
export class CubismFramework {
  /**
   * Cubism FrameworkAPI
   *  API
   *  
   *
   * @param    option      Option
   *
   * @return   true
   */
  public static startUp(option?: CubismStartupOption): boolean {
    if (s_isStarted) {
      CubismLogInfo('CubismFramework.startUp() is already done.');
      return s_isStarted;
    }

    if ((Live2DCubismCore as any)._isStarted) {
      s_isStarted = true;
      return true;
    }

    (Live2DCubismCore as any)._isStarted = true;

    s_option = option;

    if (s_option) {
      Live2DCubismCore.Logging.csmSetLogFunction(s_option.logFunction);
    }

    s_isStarted = true;

    // Live2D Cubism Core
    if (s_isStarted) {
      const version: number = Live2DCubismCore.Version.csmGetVersion();
      const major: number = (version & 0xff000000) >> 24;
      const minor: number = (version & 0x00ff0000) >> 16;
      const patch: number = version & 0x0000ffff;
      const versionNumber: number = version;

      CubismLogInfo(
        `Live2D Cubism Core version: {0}.{1}.{2} ({3})`,
        ('00' + major).slice(-2),
        ('00' + minor).slice(-2),
        ('0000' + patch).slice(-4),
        versionNumber,
      );
    }

    CubismLogInfo('CubismFramework.startUp() is complete.');

    return s_isStarted;
  }

  /**
   * StartUp()CubismFramework
   * Dispose()CubismFramework
   */
  public static cleanUp(): void {
    s_isStarted = false;
    s_isInitialized = false;
    s_option = undefined;
  }

  /**
   * Cubism Framework<br>
   *     Initialize()Dispose()
   */
  public static initialize(): void {
    if (!s_isStarted) {
      CubismLogWarning('CubismFramework is not started.');
      return;
    }

    // --- s_isInitialized ---
    // 
    // Initialize()Dispose()
    if (s_isInitialized) {
      CubismLogWarning(
        'CubismFramework.initialize() skipped, already initialized.',
      );
      return;
    }

    s_isInitialized = true;

    CubismLogInfo('CubismFramework.initialize() is complete.');
  }

  /**
   * Cubism Framework
   *      
   *      
   */
  public static dispose(): void {
    if (!s_isStarted) {
      CubismLogWarning('CubismFramework is not started.');
      return;
    }

    // --- s_isInitialized ---
    // dispose()initialize()
    if (!s_isInitialized) {
      // false...
      CubismLogWarning('CubismFramework.dispose() skipped, not initialized.');
      return;
    }

    // 
    CubismRenderer.staticRelease();

    s_isInitialized = false;

    CubismLogInfo('CubismFramework.dispose() is complete.');
  }

  /**
   * Cubism FrameworkAPI
   * @return APItrue
   */
  public static isStarted(): boolean {
    return s_isStarted;
  }

  /**
   * Cubism Framework
   * @return true
   */
  public static isInitialized(): boolean {
    return s_isInitialized;
  }

  /**
   * Core API
   *
   * @praram message 
   */
  public static coreLogFunction(message: string): void {
    // Return if logging not possible.
    if (!Live2DCubismCore.Logging.csmGetLogFunction()) {
      return;
    }

    Live2DCubismCore.Logging.csmGetLogFunction()(message);
  }

  /**
   * 
   *
   * @return  
   */
  public static getLoggingLevel(): LogLevel {
    if (s_option != null) {
      return s_option.loggingLevel;
    }
    return LogLevel.LogLevel_Off;
  }

  /**
   * 
   * 
   */
  private constructor() {}
}

export interface CubismStartupOption {
  logFunction: Live2DCubismCore.csmLogFunction; // 
  loggingLevel: LogLevel; // 
}

/**
 * 
 */
export enum LogLevel {
  LogLevel_Verbose = 0, // 
  LogLevel_Debug, // 
  LogLevel_Info, // Info
  LogLevel_Warning, // 
  LogLevel_Error, // 
  LogLevel_Off // 
}
